<template>
    <div class="breadcrumb">
      <el-breadcrumb separator=">">
      <el-breadcrumb-item class="titleHover" :to="{ path: '/' }">Home</el-breadcrumb-item>
      <el-breadcrumb-item class="titleHover" :to="{ path: '/' }">{{path[0]}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{path[1]}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>   
</template>
<script>
export default {
  data () {
    return {
      path: []
    }
  },
  mounted: function () {
    const pa = this.$route.fullPath.split('/')
    this.path = pa.slice(-2)
  },
  watch: {
    '$route' (to, from) { 
        const toDepth = to.path.split('/')
        this.path = toDepth.slice(-2)
    }
  }
}
</script>

<style>
.breadcrumb {
   margin-top: 10px;
   height: 20px;
   width:220px;
 }
 .el-breadcrumb {
   color: #595959;
   font-size: 12px;
 }
 .titleHover :hover {
   color: #E54C87;
 }
 .el-breadcrumb__item__inner {
   color: #999;
 }
 .el-breadcrumb__item:last-child .el-breadcrumb__item__inner {
   color: #999;
 }

</style>


