import Vue from 'vue'
import Router from 'vue-router'
import home from '@/page/home'
import goods from '@/page/goods'
import product from '@/page/productList'
import my from '@/page/myPage/my'
import Profile from '@/page/myPage/profile'
import Orders from '@/page/myPage/orders'
import Favorites from '@/page/myPage/favorites'
import Forgot from '@/page/myPage/forgot'
import Following from '@/page/myPage/following'
import orDetail from '@/page/myPage/orDetail'
import withdraw from '@/page/myPage/withdraw'
import about from '@/page/myPage/about'
import contact from '@/page/myPage/contact'
import coupon from '@/page/myPage/coupon'
import service from '@/page/myPage/service'
import designers from '@/page/Designers/designers'
import deDetail from '@/page/Designers/deDetail'
import newin from '@/page/newin'
import cart from '@/page/cart'
import payment from '@/page/payment'
import login from '@/page/login'
import reset from '@/page/resetpassword'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: home
    },
    {
      path: '/Login',
      name: 'login',
      component: login
    },
    {
      path: '/Product/:id',
      name: 'productList',
      component: product
      /*
      children: [
        {
          //  /user/:id/profile 
          // UserProfile  User  <router-view> 
          path: 'profile',
          component: UserProfile
        },
        {
          //  /user/:id/posts 
          // UserPosts  User  <router-view> 
          path: 'posts',
          component: UserPosts
        }
      ]
      */
    },
    {
      path: '/My',
      component: my,
      children: [
        {
          path: '/My/Profile',
          name: 'my',
          component: Profile
        },
        {
          path: '/My/Orders',
          component: Orders
        },
        {
          path: '/My/Favorites',
          name: '',
          component: Favorites
        },
        {
          path: '/My/Forgot',
          name: '',
          component: Forgot
        },
        {
          path: '/My/Following',
          name: '',
          component: Following
        },
        {
          path: '/My/Orders/Detail',
          component: orDetail,
          name: 'Orders'
        },
        {
          path: '/My/Orders/Withdraw',
          component: withdraw,
          name: 'withdraw'
        },
        {
          path: '/My/About',
          component: about,
          name: 'about'
        },
        {
          path: '/My/Contact',
          component: contact,
          name: 'contact'
        },
        {
          path: '/My/Coupon',
          component: coupon,
          name: 'coupon'
        },
        {
          path: '/My/Service',
          component: service,
          name: 'service'
        }
      ]
    },
    {
      path: '/Designers',
      component: designers,
      name: 'designers'
    },
    {
      path: '/Designers/detail',
      component: deDetail,
      name: 'Detail'
    },
    {
      path: '/NEWIN',
      component: newin,
      name: 'newin'
    },
    {
      path: '/Cart',
      component: cart,
      name: 'cart'
    },
    {
      path: '/Payment',
      component: payment,
      name: 'payment'
    },
    {
      path: '/Goods',
      component: goods,
      name: 'goods'
    },
    {
      path: '/resetpassword',
      component: reset,
      name: 'reset'
    }
  ]
})



