<template>
<!--  -->
  <div class="login-main">
    <div class="login-maintwo">
      <h2>{{ socal }}</h2>
      <a href="" @click="loginBy('http://192.168.0.112:8000/auth/google')">
        <span>
          <img src="static/images/guge.png" alt="">
        </span>
        <em>{{ guge }}</em>
      </a>
      <a href="">
        <span>
            <img src="static/images/book.png" alt="">
        </span>
        <em>{{ foker }}</em>
      </a>
    </div>
    <!--  -->
    <div class="sign">
      <h2>{{ IN }}</h2>
      <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" class="demo-ruleForm">
        <el-form-item prop="name">
          <el-input v-model="ruleForm2.name" :placeholder="mname"></el-input>
        </el-form-item>
        <el-form-item prop="pass">
          <el-input type="password" v-model="ruleForm2.pass" auto-complete="off" :placeholder="main"></el-input>
        </el-form-item>
        <el-form-item class="class">
          <el-button type="primary" @click="login('ruleForm2')">{{ dengru }}</el-button>
        </el-form-item>
      </el-form>
      <div :class="butten" data-sitekey="butter"></div>
      <el-checkbox v-model="checked">{{ stay }}</el-checkbox>
      <el-button class="sign-pass" type="text" @click="open3">{{ fort }}</el-button>
    </div>
    <strong></strong>
    <!--  -->
    <div class="gister">
      <h2>{{ REG }}</h2>
      <el-form :model="ruleForm3" :rules="rules2" ref="ruleForm3" class="demo-ruleForm">
        <el-form-item prop="mail">
          <el-input style="width:369px" v-model="ruleForm3.mail" auto-complete="off" :placeholder="youxiang2"></el-input>
        </el-form-item>
        <el-form-item prop="pasd">
          <el-input style="width:369px" type="password" v-model="ruleForm3.pasd" auto-complete="off" :placeholder="mima2"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="signup('ruleForm3')">{{ zhuce }}</el-button>
        </el-form-item>
      </el-form>
      <p>When you <span>Register,</span> you agree to our <em>User Agreement</em>
      and acknowlegge reading our <em>User Privacy Notice</em>
      </p>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
          //  
         var validateEmail = (rule, value, callback) => {
          var reg = /^[a-zA-Z0-9]([a-zA-Z0-9_-]+)@([a-zA-Z0-9]+)\.([a-zA-Z]+)$/; //
          if (value === '') {
            callback(new Error('Please enter the mailbox'));
          }else if(reg.test(value) === true){
            callback();
          } else {
           callback(new Error('Input error'))
          }
        };
      //
      var validatePassword = (rule, value, callback) => {
        var regExp = /^[a-zA-Z][a-zA-Z0-9]{7,31}$/;
        if (value === '') {
          callback(new Error('Please input a password'))
        } else if (regExp.test(value) === false) {
          callback(new Error('Password error'))
        } else {
          callback()
        }
      }
      // 
      // 
      /*
      var change = (rule, value, callback) => {
       var regExp = /[\u4e00-\u9fa5_a-zA-Z0-9_]{4,10}/ //
          if (value === '') {
            callback(new Error(''));
          }else if(regExp.test(value) === false){
            callback();
          } else {
           callback(new Error(''))
          }
      };
      */
      // 
      /*var validatePPass = (rule, value, callback) => {
          var regExp=/^[\u4e00-\u9fa5]{2,4}$/;
          if (value === '') {
            callback(new Error(''));
          }else if(regExp.test(value) === false){
            callback(new Error(''))
          } else {
            callback();
        }
      };
      */
      // 
      var validateAass = (rule, value, callback) => {
          var regExp=/^[a-zA-Z0-9]([a-zA-Z0-9_-]+)@([a-zA-Z0-9]+)\.([a-zA-Z]+)$/;
          if (value === '') {
            callback(new Error('Please enter the mailbox'));  
          }else if(regExp.test(value) === false){
            callback(new Error('Mailbox error'))
          } else {
            callback();
        }
      };
      // 
        var validateEass = (rule, value, callback) => {
        var regExp = /^[a-zA-Z][a-zA-Z0-9]{7,31}$/;
        if (value === '') {
          callback(new Error('Please input a password'))
        } else if (regExp.test(value) === false) {
          callback(new Error('8-16 letters and numbers can not be pure numbers or pure English'))
        } else {
          callback()
        }
      }
export default {
    data() {
        return {
            butten: '',
            butter: '',
            fort: 'Forgot password',
            stay: 'Stay signed in',
            mname: 'Email address',
            main: 'Passwprd',
            dengru: 'SlGN lN',
            xingshi: 'First name',
            xingming: 'Last name',
            youxiang2: 'Email address',
            mima2: 'Password',
            zhuce: 'REGlSTER',
            checked: true,
            input2:'',
            input3:'',
            input4: '',
            input5: '', 
            input6: '',
            input7: '', 
            socal: 'CONNECT WlTH SOClAL',
            guge: 'Google',
            foker: 'Facebook',
            IN: 'SlGN lN',
            REG: 'REGlSTER',
            // 
            ruleForm2: {
                pass: '',
                name: ''
            },
            // 
            ruleForm3: {
              pasd: '',
              mail: ''
            },
            // 
            rules2: {
              pass: [
                { validator: validatePassword, trigger: 'blur' }
              ],
              name: [
                { validator: validateEmail, trigger: 'blur' }
              ],
              mail: [
                { validator: validateAass, trigger: 'blur'}
              ], 
              pasd: [
                { validator: validateEass, trigger: 'blur'}
              ]
            }
          };
        },
        // 
        methods: {
          open3() {
              this.$prompt('Enter the mailbox so that we can send confirmation mail', {
                confirmButtonText: '',
                showCancelButton: false,
                customClass: 'apple',
                inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
                inputErrorMessage: 'Mailbox error'
              }).then(({ value }) => {
                axios.post('http://192.168.0.112:8000/auth/password/restore/request',{
                  email: value,
                })
                .then(function(res){
                  console.log(res)
                  if(res.data.success == false) {
                    alert("")
                  } else if(res.data.success == true) {
                    if (res.data.email_change_warning == true) {
                      alert('')
                    }
                    alert("")
                    this.$message({
                    type: 'success',
                    message: 'Your email address is: ' + value
                  });
                  }
                })
                .catch(function(err){
                  
                });
              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: 'Cancel input'
                });       
              });
          },
          // 
          signup(formName) {
            var self = this;
            var ente;
            var fromdata;
              ente = 'http://192.168.0.112:8000/auth/register'
              fromdata = {
                email: this.ruleForm3.mail,
                password: this.ruleForm3.pasd,
            }
            this.$refs[formName].validate((valid) => {
              if (valid) {
              axios({
                method: 'post',
                url: ente,
                headers: {'Content-Type': 'application/x-www-form-urlencoded'}, // application/x-www-form-urlencoded
                data: fromdata
              })
              .then(function (response) {
                console.log(response.data)
                // this.$router.push({path: '/Home'});
                if(response.data.need_captcha){
                  self.butten= 'g-recaptcha'
                  self.butter= '6LdQby8UAAAAAKyVYq-g2kCEIcK96DL82DCLcqfK'
                }
                if(response.data.success == false) {
                  alert("")
                  if(response.data.message == 'email existing') {
                    alert("")
                    self.$refs['ruleForm3'].resetFields()
                  } 
                  if(response.data.captcha_success ==false) {
                    alert("")
                  }
                } else if(response.data.success == true) {
                  alert("")
                  self.$refs['ruleForm3'].resetFields()
                }
                })
                .catch(function (error) {
                  console.log(error);
                  alert('shibai')
                  // console.log(data)
                });
              } else {
                console.log('error submit!!')
                return false;
                self.$refs['ruleForm2'].resetFields()
  
              }
            });
          },
          // 
          login(formName) {
            var self = this;
            var ente;
            var fromdata;
              ente = 'http://192.168.0.112:8000/auth/login'
              fromdata = {
                email: this.ruleForm2.name,
                password: this.ruleForm2.pass,
            }
            this.$refs[formName].validate((valid) => {
              if (valid) {
              axios({
                method: 'post',
                url: ente,
                headers: {'Content-Type': 'application/x-www-form-urlencoded'}, // application/x-www-form-urlencoded,
                data: fromdata,
              })
              .then(function (response) {
                console.log(response.data)
                if(response.data.need_captcha){
                  self.butten= 'g-recaptcha'
                  self.butter= '6LdQby8UAAAAAKyVYq-g2kCEIcK96DL82DCLcqfK'
                }
                if(response.data.success == false) {
                  alert("")
                  if(response.data.message == 'verify error') {
                    alert("")
                    self.ruleForm2.pass = ''                   
                  } 
                  if(response.data.captcha_success ==false) {
                    alert("")
                  }
                } else if(response.data.success == true) {
                  window.localStorage.setItem('token', response.data.olytoken)
                  axios.defaults.headers['olytoken'] = window.localStorage.getItem('token')
                  alert("")
                  self.$router.push({path: '/'});
                  // self.$router.push({path: '/Home'});
                }
                })
                .catch(function (error) {
                  console.log(error);
                  // self.$router.push({path: '/'});
                  // console.log(data)
                });
              } else {
                console.log('error submit!!')
                return false;
                self.$refs['ruleForm2'].resetFields()
              }
            });
          },

          //
          loginBy(api) {
            axios({
                method: 'get',
                url: api,
                headers: {'Content-Type': 'application/x-www-form-urlencoded'} // application/x-www-form-urlencoded,
              })
              .then(function(res) {
                console.log(res)
              })
              .catch(function (error) {
                console.log(error)
                alert('err')
              })
          }
        }
    }
</script>

<style>
.login-main {
    width: 1080px;
    height: 654px;
    margin: 0 auto;
    margin-top: 20px;

}
/* face */
.login-maintwo {
  height: 44px;
  padding: 20px 314px;
  border-bottom: 1px solid #e5e5e5;
}
.login-maintwo h2 {
  float: left;
  font-size: 12px;
  margin-top: 18px;
}
.login-maintwo a {
  display: block;
  float: left;
  font-size: 18px;
  width: 110px;
  height: 34px;
  padding: 10px 0 0 20px;
  margin-left: 20px;
  line-height: 24px;
  background: #4f95f7;
  color: white;
}
.login-maintwo a:nth-child(3) {
  width: 122px;
  background: #4467af;
}
.login-maintwo img {
  float: left;
  width: 15px;
  height: 20px;
}
.login-maintwo span {
  float: left;
  width: 21px;
  height: 21px;
  margin-right: 8px;
  padding: 2px 2px 2px 2px;
  background: white;
}
.login-main strong {
  float: left;
  display: block;
  height: 290px;
  margin-top: 20px;
  border-left: 1px solid #e5e5e5;
}
/*  */
.sign {
  float: left;
  width: 369px;
  height: 331px;
  margin: 39px 85px 0 85px;
}
.sign h2 {
  font-size: 16px;
  margin-bottom: 20px;
  text-align: center;
}
.sign .el-input__inner {
  border-radius: 0;
}
.class .el-button {
  width: 100%;
  margin-bottom: 10px;
  border-radius: 0;
  border: 0;
  background: #e54c87;
}
.sign .el-checkbox__label {
  font-size: 12px;
}
.sign-pass {
  float: right;
  padding: 5px 0 0 0;
  font-size: 12px;
  color: black;
}
.sign .el-form-item:nth-child(3) {
  margin-bottom: 0px;
}
.sign .el-checkbox__inner {
    width: 12px;
    height: 12px;
    border-radius: 0;
    border-color: #b7b7b7;
}
.sign .el-checkbox__input.is-checked .el-checkbox__inner {
  background: white;
  border-color: #b7b7b7;
}
.sign .el-checkbox__input .is-focus {
  border-color: #b7b7b7;
}
.sign .el-checkbox__inner::after {
  left: 2px;
  top: -1px;
  border-color: #e54c87;
}
/*  */
.gister {
  float: left;
  width: 369px;
  height: 331px;
  margin: 39px 85px 0 85px;
}
.gister h2 {
  font-size: 16px;
  text-align: center;
  margin-bottom: 20px;
}
.gister .el-input__inner {
  border-radius: 0;
}
.gister .el-button {
  float: left;
  width: 369px;
  border-radius: 0;
  border: 0;
  background: #e54c87;
}
.gister p {
  font-size: 12px;
}
.gister span {
  font-weight: bold;
}
.gister em {
  font-weight: bold;
  color: #e54c87;
}
.gister .el-form demo-ruleForm {
  width: 350px;
}
.class {
  position: relative;
  top: 0;
}
.el-message-box {
  width: 400px;
  height: 178px;
}
.el-message-box__content {
  padding: 0;
}
.el-message-box__headerbtn {
    position: absolute;
    top: 19px;
    background: 0 0;
    border: none;
    outline: 0;
    padding: 0;
    cursor: pointer;
}
.el-icon-close ::before {
  width: 10px;
  height: 10px;
}
.el-message-box__headerbtn .el-message-box__close:hover {
  color: #F85292;
  opacity: 0.5;
}
.el-message-box .el-button--primary {
  position: relative;
  width: 90px;
  height: 26px;
  top: -54px;
  left: -29px;
  font-size: 14px;
  line-height: 0;
  border: 0;
  border-radius: 0;
  background: #F85292;
}
.el-message-box .el-input__inner {
  width: 230px;
  height: 26px;
  border-radius: 0;
}
.el-message-box {
  border-radius: 0;
}
.el-message-box .el-input {
  float: left;
  left: 31px;
}
.el-message-box__input {
  float: left;
}
.el-message-box p {
  font-size: 12px;
  margin-left: 30px;
  margin-top: 25px;
  color: #0F0F0F;
} 
.el-message-box .el-message-box__errormsg {
  float: left;
  margin-top: 10px;
  margin-left: 100px;
}
.v-modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: .5;
    background: rgba(0,0,0,0.2);
}
</style>


