<template>
<!--  -->
  <div class="loctionnav" id="nav">
    <div class="nav-loction">
      <ul>
          <span><strong>|</strong></span>
          <li  v-for="(tab,index) in tabs" :key="index">
            <router-link class="linkTo" :to="tab.to">  <!-- @mouseenter.native="childShow(index)-->
              {{ tab.text }}
            </router-link>
          </li>
      </ul>
    </div>
    <gate v-if="childshow" @mouseleave.native="childhidden"></gate>
  </div>
</template>

<script>
import gate from '@/components/navigate' //div
export default {
   components: {
      'gate': gate
  },
  data() {
    return {
      childshow: false,
      tabs: [
        { text: 'NEW IN', to:'/NEWIN' },
        { text: 'FLASH SAIE', to:'/Product/FLASHSAIE' },
        { text: 'DESIGNERS',to:'/Designers'},
        { text: 'MEMBER',to:'/Product/MEMBER'},
        { text: 'DRESSES',to:'/Product/DRESSES'},
        { text: 'TOPS',to:'/Product/TOPS'},
        { text: 'BOTTOMS',to:'/Product/BOTTOMS'},
        { text: 'LINEN CLOTH',to:'/Product/LINENCLOTH'},
        { text: 'OUTERWEAR',to:'/Product/OUTERWEAR'},
        { text: 'JUMPSUITS',to:'/Product/JUMPSUITS'},
        { text: 'SWIMWEAR',to:'/Product/SWIMWEAR'},
        { text: 'SHOES',to:'/Product/SHOES'},
        { text: 'BAGS',to:'/Product/BAGS'},
        { text: 'ACC',to:'/Product/ACC'},
        { text: 'SALE',to:'/Product/SALE'}
      ]
    }
  },
  created() {
    window.addEventListener('scroll',this.menuFixed) //
  },
  methods: {
      menuFixed: function (nav){ //
      var obj = document.getElementById('nav');
      var scrolled = window.pageYOffset < 125;
      if (scrolled) {
        obj.style.position = 'static'
      } else {
          obj.style.position = 'fixed'
          obj.style.top = 0
      }
    },
    /*childShow: function (index) {
      if(index !== 0) {
        this.childshow = true
      }
  },*/
    childhidden: function () {
      this.childshow = false
    } 
  }
}
</script>
<style scoped>
/*  */
.loctionnav {
  position: relative;
  width: 100%;
  height: 40px;
  z-index: 1000;
  line-height: 40px;
  background: #fafafa;
  border-bottom: 1px solid #e5e5e5;
}
/* div */
.nav-loction {
  width: 1080px;
  height: 31px;
  margin: 0 auto;
}
.nav-loction ul {
  float: left;
}
.nav-loction li {
  font-size: 12px;
  float: left;
  margin-right: 15px;
}
/* 16li */
.nav-loction li:nth-child(16) {
  margin: 0;
}
/* | */
.nav-loction strong {
  float: left;
  position: relative;
  left: 231px;
  top: -2px;
  color: #b7b7b7;
}
.linkTo:hover {
  color: #E54C87
}
.linkTo:focus {
  color: #E54C87
}
</style>


