<template>
<!--  -->
  <div class="secondheader">
      <!-- logoinput -->
      <div class="secondheader-nav">
          <a :href="url"><img class="funtter" v-bind:src="pictt" alt=""></a>
            <el-input
                placeholder="Search..."
                icon="search"
                v-model="input2"
                :on-icon-click="handleIconClick">
            </el-input>
            <!--  -->
            <ul class="banana">
                <li><strong>|</strong><router-link to="/login">{{loginmess}}</a></router-link></li>
                <!--  -->
                <li><strong>|</strong><router-link :to="{ path: '/My/Favorites', params: { page: 2 }}"><img src="static/images/aixin.png" alt=""></router-link></li>
                <li><strong>|</strong><router-link to="/Cart"><img src="static/images/gouwuche.png" alt=""></router-link></li>
            </ul>
      </div>
  </div>
</template>

<script>
export default {
    data() {
        return {
            loginmess: null,
            input2: '',
            url: '/',
            pictt: 'static/images/wenzi.png'
        }
    },
    methods: {
        handleIconClick(ev) {
        console.log(ev);
    }
  },
  created: function () {
      this.loginmess = 'sdsds'
  }
}
</script>

<style>
.secondheader {
    border-top: 1px solid #b7b7b7;
    width: 100%;
    height: 95px;
    background: #fafafa;
}
/* logoinput*/
.secondheader-nav {
    width: 1080px;
    height: 53px;
    margin: 0 auto;
    padding: 21px 0 21px 0;
}
.secondheader-nav .funtter {
    float: left;
    width: 101px;
    height: 53px;
}
.secondheader-nav .el-input {
    float: left;
    width: 524px;
    margin: 12px 0 0 225px;
}
.secondheader-nav a {
    float: left;
    font-size: 12px;
    line-height: 24px;
}
/*  */
.secondheader-nav .banana {
    float: right;
}
.secondheader-nav .banana img {
    float: left;
    width: 13px;
    margin-top: 4px;
}
.secondheader-nav strong {
    float: left;
    color: #e5e5e5;
    margin: 0 14px 0 14px;
}
.secondheader-nav li {
    float: left;
    margin-top: 17px;
}
.secondheader-nav .el-input__icon+.el-input__inner {
    border-radius: 0;
}
.secondheader-nav .el-input__icon+.el-input__inner:focus {
    border-color: #E54C87;
}
.banana li:hover{
    color: #E54C87;
}
</style>


